.form-wrapper {
    max-width: 600px;
    margin: auto;
    box-shadow: 0px 2px 15px rgba(0,0,0,.3);
    padding: 15px;
    margin-top: 50px;
    border-radius: 10px;
    background: #fff;
}
.App {
    background: #eee;
    height: 100vh;
}

.btn {
    padding: 5px 30px !important;
    border-radius: 50px !important;
}
.error-message {
    margin-top: 15px;
}
.sidebar {
    background: #fff;
    float: left;
    width: 300px;
    height: 100vh;
    border-right: 1px solid #ddd;
    position: fixed;
    top: 0px;
}
.main-view {
    position: fixed;
    left: 300px;
    background: #fafafa;
    height: calc(100vh - 82px);
    width: calc(100% - 300px);
    padding-top: 10px;
    overflow: hidden;
    overflow-y: auto;
    top: 0;
    padding-bottom: 10px;
    margin-top: 40px;
}
.settings-view {
    position: fixed;
    left: 300px;
    background: #fafafa;
    width: calc(100% - 300px);
    overflow: hidden;
    overflow-y: auto;
    top: 0;
    margin-top: 40px;
    height: 100%;
}
ul.thread-list {
    list-style: none;
    padding: 0px;
}
ul.thread-list li a .zmdi {
    float: left;
    font-size: 48px;
    padding: 5px 15px;
    color: #ccc;
}
ul.thread-list li a {
    text-decoration: none;
    color: #444;
}
u.thread-list li a h5 {
    font-size: 14px;
    padding: 10px;
    font-weight: 700;
    padding-bottom: 0px;
    margin-bottom: 0px;
}
ul.thread-list li a p {
    font-size: 14px;
    color: #999
}
ul.thread-list label {
    padding-left: 20px;
    font-size: 12px;
    color: #aaa;
    padding-top: 10px;
}
.input-view {
    position: fixed;
    left: 300px;
    width: 100%;
    bottom: 0px;
}
.input-view .form-control {
    border: none;
    border-radius: 0px;
    border-top: 1px solid #eee;
    font-size: 14px;
    padding: 10px;
    height: auto;
    padding-left: 35px;
}
.search-container .form-control {
    border-radius: 20px;
    margin: 10px;
    width: 90%;
    background: #eaeaea;
    text-align: left;
}
.search-container .search-button {
    width: 30px;
    height: 30px;
    border: none;
    color: #888;
    background: none;
    position: fixed;
    top: 15px;
    left: 240px;
    border-left: 1px solid;
}
.thread-list .title {
    font-size: 1rem;
    padding-bottom: 0px;
    padding-top: 8px;
    margin-bottom: 2px;
}
.send-button {
    position: fixed;
    border: none;
    border-left: 1px solid;
    background: none;
    color: #aaa;
    right: 5px;
    margin-top: 7px;
}
.message-item {
    padding: 10px;
}
.message-item.msg-right {
    text-align: right;
}
.message-item.msg-right .chat-bubble {
    display: inline-block;
    border-radius: 15px;
    padding: 5px 15px;
    background: #a56dff;
    margin: 0px 5px;
    color: #fff;
    max-width: 60%;
    text-align: left;
}
.message-item.msg-left .chat-bubble {
    display: inline-block;
    /* border: 3px solid;
    border-color: #ccc; */
    border-radius: 15px;
    padding: 5px 15px;
    background: #dedddd;
    margin: 0px 5px;
    max-width: 60%;
    text-align: left;
}
.chat-image {
    width: 100%;
    margin: 0px 5px;
    border-radius: 15px;
    max-width: 60%;
    
}
.message-item.msg-right .message-icon {
    float: right;
    margin-top: 1px;
    font-size: 30px;
    width: 20px;
    height: 20px;
    color: black;
}
.message-item.msg-left .message-icon {
    float: left;
    margin-top: 15px;
    font-size: 30px;
    width: 20px;
    height: 20px;
    color: black;
}
.message-item.msg-left .username-text {
    font-size: 10px;
    color: #777;
    margin-left: 35px;
}
.thread-top-bar{
    position: absolute;
    width: calc(100% - 300px);
    height: 40px;
    top: 0;
    right: 0;
    background: #dadada;
    display: inline-flex;
    align-items: center;
    justify-content: space-between;
}
.thread-top-bar .icons {
    display: inline-flex;
    padding-right: 10px;
}
.thread-top-bar .thread-title {
    padding-left: 10px;
}
.thread-top-bar .thread-avatar {
    width: 25px;
    height: 25px;
}
.input-group .input-image {
    position: fixed;
    bottom: 7px;
    margin-left: 4px;
    color: #aaa;
    border:none;
    background: none;
}
.sidebar .account-settings {
    position: fixed;
    left: 5px;
    bottom: 5px;
    display: flex;
    flex-direction: row;
}
.sidebar .account-settings .settings-icon{

}
.account .account-row{
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    height: 200px;
}
.account .account-form {
    width: 50%;
}
.account .account-avatar {
    color:rgba(0, 0, 0, 0);
}
.account .account-avatar:hover {
    color:#aaa;
}
.account .account-avatar .edit-icon {
    position: absolute;
    right: 205px;
    top: 200px;
}
.account .account-avatar .account-avatar-icon {
    height: 100%;
    width: auto;
    color: #aaa;
    max-width: 200px;
    position: static;
    opacity: 1;
}
.account .account-avatar .account-avatar-icon:hover {
    opacity:0.5;
}
.account .account-row .account-col {
    display: flex;
    flex-direction: column;
    width: 50%;
}
.sidebar-avatar {
    margin: 8px 5px 0px 20px;
}
.settings-nickname-icon {
    margin-right: 20px;
    margin-top: 8px;
}
.title-screen {
    width: calc(100% - 300px);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    align-content: center;
    float: right;
    height: 100vh;
}
.title-screen .title {
    font-size: 30px;
}
.sidebar-bar {
    padding: 5px 0px;
    background: none;
    border-top: 1px solid #ddd;
    border-bottom: 1px solid #ddd;
}
.sidebar-bar .sidebar-button {
    background: #a56dff;
    border: none;
    border-radius: 30px;
    margin: 0px 5px;
    color: white;
    padding: 2px 8px;
}
.sidebar-bar .sidebar-button.unselected {
    background: #afafaf;
}
.group-creator .button {
    background: #a56dff;
    border: none;
    border-radius: 30px;
    margin: 0px 5px;
    color: white;
    padding: 5px 15px;
}
.group-creator .button.unselected {
    background: #afafaf;
    max-width: 63px;
}